
.pin[data-v-1dae05ca]{
   text-align: left;
   background: #FFF;
   border: 1px solid #ccc;
   -webkit-box-shadow: 0px 0px 6px #ccc;
           box-shadow: 0px 0px 6px #ccc; /*中间投影*/
   border-radius: 5px; /*圆角*/
   position: absolute;
}
.pin a[data-v-1dae05ca]{
   width: 100%;
}
.pin>a>img[data-v-1dae05ca]{
   width:100%;
   vertical-align: middle;
}
.hr[data-v-1dae05ca]{
   width: 90%;
   background-color: antiquewhite;
   height: 2px;
   margin: 4px auto;
}
.pin .content[data-v-1dae05ca]{
   position: relative;
   width: 90%;
   margin: 4px auto;
   padding: 5px 4px;
   text-align: left;
}
.pin .content .title[data-v-1dae05ca]{
   position: relative;
   font-size: 15px;
  text-align: left;
   width: -webkit-fit-content;
   width: -moz-fit-content;
   width: fit-content;
   display: inline-block;
   border-style: none;
}
.pin .content img[data-v-1dae05ca]{
   position: relative;
   height: 20px;
   top:4px;
   left: 3px;
   display: inline-block;
   border-style: none;
   width: 20px;
}
.pin .content .stars[data-v-1dae05ca]{
   position: relative;
   font-size: 9px;
   text-align: left;
   margin: 5px 0 0 4px;
   display: inline-block;
   width: -webkit-fit-content;
   width: -moz-fit-content;
   width: fit-content;
   border-style: none;
}
.pin .content .des[data-v-1dae05ca]{
   width:100%;
   font-size: 12px;
}
 /*设置每一个瀑布流块中的图像样式*/
.pin  .userinfo[data-v-1dae05ca]{
  position: relative;
   width: 100%;
   height: 40px;
   text-align: left;
}
.pin  .userinfo>img[data-v-1dae05ca]{
   position: relative;
   width: 34px;
   height: 34px;
   border-radius: 50%;
   overflow: hidden;
   top:50%;
   -webkit-transform: translate(8px,-50%);
           transform: translate(8px,-50%);
   display: inline-block;
}
.pin .userinfo .name[data-v-1dae05ca]{
   font-size: 14px;
   position:absolute;
   z-index: 5;
   top:50%;
   display: inline-block;
   -webkit-transform: translate(15px,-50%);
           transform: translate(15px,-50%);
}



